import * as Yup from 'yup';
import PropTypes from 'prop-types';
import React, { useCallback } from 'react';
import { useSnackbar } from 'notistack5';
import { useNavigate } from 'react-router-dom';
import { FieldArray, Form, FormikProvider, getIn, useFormik, validateYupSchema, yupToFormErrors } from 'formik';
import { Icon } from '@iconify/react';
import PlusFill from '@iconify/icons-eva/plus-fill';
// material
import { Delete as DeleteIcon, Save as SaveIcon } from '@material-ui/icons';
import { LoadingButton } from '@material-ui/lab';
import {
  Box,
  Card,
  Grid,
  Stack,
  TextField,
  Typography,
  FormHelperText,
  IconButton,
  CardHeader,
  CardContent,
  Button,
  Autocomplete,
  InputAdornment,
} from '@material-ui/core';
// utils
import { fData } from '../../utils/formatNumber';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// utils
import { nameRegExp } from '../../utils/constants/regexs';
// components
import { UploadAvatar } from '../upload';

// ----------------------------------------------------------------------

const REWARD_TYPES = ['Easter Egg', 'Present', 'Chest', 'Lottery Wheel'];
const tokenValues = {
  tokenValue: 44,
  starValueInTokens: 2,
  keyValueInTokens: 10,
  boosterValueInTokens: 20,
  coinValueInTokens: 1,
};

const initialContent = {
  title: 'Basic',
  frequency: 50,
  numberOfKeys: 5,
  numberOfCoins: 5,
  numberOfStars: 5,
  numberOfBoosters: 5,
};

const NewRewardSchema = Yup.object().shape({
  avatarUrl: Yup.mixed().required('Avatar is required'),
  name: Yup.string().matches(nameRegExp, 'Cannot include special characters.').required('Name is required'),
  type: Yup.string(),
  location: Yup.string(),
  contents: Yup.array().of(
    Yup.object().shape({
      title: Yup.string(),
      frequency: Yup.number()
        .min(1)
        .max(100)
        .test('total frequency test', 'total frequencies must be exactly 100%', function validateFrequency() {
          const {
            options: {
              context: { contents },
            },
          } = this;
          const total = contents.reduce((acc, content) => acc + content.frequency, 0);
          return total === 100;
        }),
      numberOfKeys: Yup.number(),
      numberOfCoins: Yup.number(),
      numberOfStars: Yup.number(),
      numberOfBoosters: Yup.number(),
    })
  ),
});

RewardNewForm.propTypes = {
  isEdit: PropTypes.bool,
  currentReward: PropTypes.object,
};

export default function RewardNewForm({ isEdit, currentReward }) {
  const navigate = useNavigate();
  const { enqueueSnackbar } = useSnackbar();

  const formik = useFormik({
    enableReinitialize: true,
    initialValues: {
      avatarUrl: currentReward?.avatarUrl || null,
      name: currentReward?.name || 'Reward Name',
      type: currentReward?.type || 'Easter Egg',
      location: currentReward?.location || 'Unassigned',
      contents: currentReward?.contents || [{ ...initialContent }],
    },
    // validationSchema: NewRewardSchema,
    validate: async (values) => {
      try {
        await validateYupSchema(values, NewRewardSchema, true, values);
      } catch (err) {
        return yupToFormErrors(err);
      }
      return {};
    },
    onSubmit: async (values, { setSubmitting, resetForm, setErrors }) => {
      alert(JSON.stringify(values));
      try {
        resetForm();
        setSubmitting(false);
        enqueueSnackbar(!isEdit ? 'Create success' : 'Update success', { variant: 'success' });
        navigate(PATH_DASHBOARD.reward.list);
      } catch (error) {
        console.error(error);
        setSubmitting(false);
        setErrors(error);
      }
    },
  });

  const { errors, values, touched, handleSubmit, isSubmitting, setFieldValue, getFieldProps } = formik;

  const handleAvatarDrop = useCallback(
    (acceptedFiles) => {
      const file = acceptedFiles[0];
      if (file) {
        setFieldValue('avatarUrl', {
          ...file,
          preview: URL.createObjectURL(file),
        });
      }
    },
    [setFieldValue]
  );

  const calculateAverageValue = () =>
    values.contents.reduce((acc, content) => {
      const totalValue =
        tokenValues.keyValueInTokens * content.numberOfKeys +
        tokenValues.starValueInTokens * content.numberOfStars +
        tokenValues.coinValueInTokens * content.numberOfCoins +
        tokenValues.boosterValueInTokens * content.numberOfBoosters;

      return acc + content.frequency * totalValue;
    }, 0);

  return (
    <FormikProvider value={formik}>
      <Form noValidate autoComplete="off" onSubmit={handleSubmit}>
        <Grid container spacing={3}>
          <Grid item md={12}>
            <Grid container spacing={3}>
              <Grid item xs={12} md={4}>
                <Card sx={{ height: '100%' }}>
                  <CardHeader title="Reward Image/Icon" titleTypographyProps={{ variant: 'subtitle2' }} />
                  <CardContent>
                    <Box>
                      <UploadAvatar
                        accept="image/*"
                        file={values.avatarUrl}
                        maxSize={3145728}
                        onDrop={handleAvatarDrop}
                        error={Boolean(touched.avatarUrl && errors.avatarUrl)}
                        caption={
                          <Typography
                            variant="caption"
                            sx={{
                              mt: 2,
                              mx: 'auto',
                              display: 'block',
                              textAlign: 'center',
                              color: 'text.secondary',
                            }}
                          >
                            Allowed *.jpeg, *.jpg, *.png, *.gif
                            <br /> max size of {fData(3145728)}
                          </Typography>
                        }
                      />
                      <FormHelperText error sx={{ px: 2, textAlign: 'center' }}>
                        {touched.avatarUrl && errors.avatarUrl}
                      </FormHelperText>
                    </Box>
                  </CardContent>
                </Card>
              </Grid>
              <Grid item xs={12} md={8}>
                <Card sx={{ height: '100%' }}>
                  <CardContent>
                    <Stack spacing={2}>
                      <TextField
                        fullWidth
                        size="small"
                        label="Name"
                        {...getFieldProps('name')}
                        error={Boolean(touched.name && errors.name)}
                        helperText={touched.name && errors.name}
                      />

                      <Stack direction="row" spacing={2}>
                        <TextField
                          select
                          fullWidth
                          size="small"
                          label="Reward Type"
                          placeholder="Reward Type"
                          {...getFieldProps('type')}
                          SelectProps={{ native: true }}
                          error={Boolean(touched.type && errors.type)}
                          helperText={touched.type && errors.type}
                        >
                          {REWARD_TYPES.map((option) => (
                            <option key={option} value={option}>
                              {option}
                            </option>
                          ))}
                        </TextField>

                        <Autocomplete
                          fullWidth
                          size="small"
                          freeSolo
                          value={values.location}
                          onChange={(event, newValue) => {
                            setFieldValue('location', newValue);
                          }}
                          options={['Default', 'Referral', 'Unassigned']}
                          renderInput={(params) => (
                            <TextField
                              {...params}
                              label="Location"
                              error={Boolean(touched.location && errors.location)}
                              helperText={
                                (touched.location && errors.location) || 'Type and enter to add new location.'
                              }
                            />
                          )}
                        />

                        {/* <TextField
                          select
                          fullWidth
                          size="small"
                          label="Location"
                          placeholder="Location"
                          {...getFieldProps('location')}
                          SelectProps={{ native: true }}
                          error={Boolean(touched.location && errors.location)}
                          helperText={touched.location && errors.location}
                        >
                          {['Default', 'Unassigned'].map((option) => (
                            <option key={option} value={option}>
                              {option}
                            </option>
                          ))}
                        </TextField> */}
                      </Stack>

                      <Typography>
                        Average value of single selection in this set = {calculateAverageValue()} tokens
                      </Typography>
                    </Stack>
                  </CardContent>
                </Card>
              </Grid>
            </Grid>
          </Grid>

          <Grid item md={12}>
            <FieldArray
              name="contents"
              render={(contentsArrayHelpers) => (
                <Card>
                  <CardHeader
                    title="Contents"
                    action={
                      <Button
                        variant="contained"
                        startIcon={<Icon icon={PlusFill} />}
                        onClick={() => contentsArrayHelpers.push(initialContent)}
                      >
                        Add Option
                      </Button>
                    }
                  />
                  <CardContent>
                    <Grid container spacing={1}>
                      {values.contents.map((content, contentIndex) => {
                        const fieldNamePrefix = `contents.${contentIndex}`;
                        const totalValue =
                          tokenValues.keyValueInTokens * content.numberOfKeys +
                          tokenValues.starValueInTokens * content.numberOfStars +
                          tokenValues.coinValueInTokens * content.numberOfCoins +
                          tokenValues.boosterValueInTokens * content.numberOfBoosters;
                        return (
                          <Grid key={contentIndex} item xs={12} md={3}>
                            <Card>
                              <CardContent>
                                <Stack spacing={1}>
                                  <TextField
                                    fullWidth
                                    variant="standard"
                                    size="small"
                                    label="Title"
                                    {...getFieldProps(`${fieldNamePrefix}.title`)}
                                    error={Boolean(
                                      getIn(touched, `${fieldNamePrefix}.title`) &&
                                        getIn(errors, `${fieldNamePrefix}.title`)
                                    )}
                                    helperText={
                                      getIn(touched, `${fieldNamePrefix}.title`) &&
                                      getIn(errors, `${fieldNamePrefix}.title`)
                                    }
                                  />
                                  <TextField
                                    fullWidth
                                    type="number"
                                    variant="standard"
                                    size="small"
                                    label="Frequency"
                                    {...getFieldProps(`${fieldNamePrefix}.frequency`)}
                                    error={Boolean(
                                      getIn(touched, `${fieldNamePrefix}.frequency`) &&
                                        getIn(errors, `${fieldNamePrefix}.frequency`)
                                    )}
                                    helperText={
                                      getIn(touched, `${fieldNamePrefix}.frequency`) &&
                                      getIn(errors, `${fieldNamePrefix}.frequency`)
                                    }
                                  />
                                  <TextField
                                    fullWidth
                                    type="number"
                                    variant="standard"
                                    size="small"
                                    label="Number of Keys"
                                    InputProps={{
                                      endAdornment: (
                                        <InputAdornment position="end">
                                          = {tokenValues.keyValueInTokens * content.numberOfKeys} tokens
                                        </InputAdornment>
                                      ),
                                    }}
                                    {...getFieldProps(`${fieldNamePrefix}.numberOfKeys`)}
                                    error={Boolean(
                                      getIn(touched, `${fieldNamePrefix}.numberOfKeys`) &&
                                        getIn(errors, `${fieldNamePrefix}.numberOfKeys`)
                                    )}
                                    helperText={
                                      getIn(touched, `${fieldNamePrefix}.numberOfKeys`) &&
                                      getIn(errors, `${fieldNamePrefix}.numberOfKeys`)
                                    }
                                  />
                                  <TextField
                                    fullWidth
                                    type="number"
                                    variant="standard"
                                    size="small"
                                    label="Number of Stars"
                                    InputProps={{
                                      endAdornment: (
                                        <InputAdornment position="end">
                                          = {tokenValues.starValueInTokens * content.numberOfStars} tokens
                                        </InputAdornment>
                                      ),
                                    }}
                                    {...getFieldProps(`${fieldNamePrefix}.numberOfStars`)}
                                    error={Boolean(
                                      getIn(touched, `${fieldNamePrefix}.numberOfStars`) &&
                                        getIn(errors, `${fieldNamePrefix}.numberOfStars`)
                                    )}
                                    helperText={
                                      getIn(touched, `${fieldNamePrefix}.numberOfStars`) &&
                                      getIn(errors, `${fieldNamePrefix}.numberOfStars`)
                                    }
                                  />
                                  <TextField
                                    fullWidth
                                    type="number"
                                    variant="standard"
                                    size="small"
                                    label="Number of Coins"
                                    InputProps={{
                                      endAdornment: (
                                        <InputAdornment position="end">
                                          = {tokenValues.coinValueInTokens * content.numberOfCoins} tokens
                                        </InputAdornment>
                                      ),
                                    }}
                                    {...getFieldProps(`${fieldNamePrefix}.numberOfCoins`)}
                                    error={Boolean(
                                      getIn(touched, `${fieldNamePrefix}.numberOfCoins`) &&
                                        getIn(errors, `${fieldNamePrefix}.numberOfCoins`)
                                    )}
                                    helperText={
                                      getIn(touched, `${fieldNamePrefix}.numberOfCoins`) &&
                                      getIn(errors, `${fieldNamePrefix}.numberOfCoins`)
                                    }
                                  />
                                  <TextField
                                    fullWidth
                                    type="number"
                                    variant="standard"
                                    size="small"
                                    label="Number of Boosters"
                                    InputProps={{
                                      endAdornment: (
                                        <InputAdornment position="end">
                                          = {tokenValues.boosterValueInTokens * content.numberOfBoosters} tokens
                                        </InputAdornment>
                                      ),
                                    }}
                                    {...getFieldProps(`${fieldNamePrefix}.numberOfBoosters`)}
                                    error={Boolean(
                                      getIn(touched, `${fieldNamePrefix}.numberOfBoosters`) &&
                                        getIn(errors, `${fieldNamePrefix}.numberOfBoosters`)
                                    )}
                                    helperText={
                                      getIn(touched, `${fieldNamePrefix}.numberOfBoosters`) &&
                                      getIn(errors, `${fieldNamePrefix}.numberOfBoosters`)
                                    }
                                  />
                                  <IconButton
                                    color="error"
                                    aria-label="delete"
                                    onClick={() => contentsArrayHelpers.remove(contentIndex)}
                                  >
                                    <DeleteIcon />
                                  </IconButton>
                                  <Stack direction="row" justifyContent="space-between">
                                    <Typography>Token value: </Typography>
                                    <Typography>{totalValue} tokens</Typography>
                                  </Stack>
                                </Stack>
                              </CardContent>
                            </Card>
                          </Grid>
                        );
                      })}
                    </Grid>
                  </CardContent>
                </Card>
              )}
            />
            <Box sx={{ mt: 3, display: 'flex', justifyContent: 'flex-end' }}>
              <LoadingButton type="submit" variant="contained" loading={isSubmitting} endIcon={<SaveIcon />}>
                Save
              </LoadingButton>
            </Box>
          </Grid>
        </Grid>
      </Form>
    </FormikProvider>
  );
}
