import { useParams, useLocation } from 'react-router-dom';
// material
import { Container } from '@material-ui/core';
// routes
import { PATH_DASHBOARD } from '../routes/paths';
// hooks
import useSettings from '../hooks/useSettings';
// components
import Page from '../components/Page';
import HeaderBreadcrumbs from '../components/HeaderBreadcrumbs';
import RewardNewForm from '../components/reward/RewardNewForm';
import { mockImgProduct } from '../utils/mockImages';

// ----------------------------------------------------------------------

export default function RewardCreate() {
  const { themeStretch } = useSettings();
  const { pathname } = useLocation();
  const { name } = useParams();
  const isEdit = pathname.includes('edit');

  // TODO: will be updated with reward info
  const currentReward = isEdit
    ? {
        avatarUrl: mockImgProduct(1),
        name: 'Easter Egg',
        location: 'Turkey',
        contents: [
          {
            title: 'Basic',
            frequency: 30,
            numberOfKeys: 5,
            numberOfCoins: 5,
            numberOfStars: 5,
            numberOfBoosters: 5,
          },
          {
            title: 'Rare',
            frequency: 30,
            numberOfKeys: 10,
            numberOfCoins: 15,
            numberOfStars: 5,
            numberOfBoosters: 10,
          },
          {
            title: 'Epic',
            frequency: 20,
            numberOfKeys: 20,
            numberOfCoins: 30,
            numberOfStars: 20,
            numberOfBoosters: 25,
          },
        ],
      }
    : {};

  return (
    <Page title="Reward: Create a new reward">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading={!isEdit ? 'Add Reward' : 'Edit Reward'}
          links={[{ name: 'Reward', href: PATH_DASHBOARD.root }, { name: !isEdit ? 'Add Reward' : name }]}
        />
        <RewardNewForm isEdit={isEdit} currentReward={currentReward} />
      </Container>
    </Page>
  );
}
